import { mapActions } from 'vuex'
import Page from '@/components/Page'

export default {
  components: {
    Page
  },
  data () {
    return {
      name: ''
    }
  },
  methods: {
    ...mapActions({
      set: 'user/setUser'
    }),
    async submitHandler (data) {
      this.$nuxt.$loading.start()
      await this.set(data.name)
      this.$router.push('/user/end')
      this.$nuxt.$loading.finish()
    }
  }
}
